.partner-card {
  display: block;
  width: 100%;
  padding: 16px 8px;
  text-align: center;
  box-shadow: 0 0 0 1px #e6e6e6;
  border-radius: 2px;
  line-height: 1;
  font-size: 12px; }
  @media screen and (min-width: 48em) {
    .partner-card {
      padding: 32px 16px;
      font-size: 16px; } }
  .partner-card:hover, .partner-card:focus {
    box-shadow: 0 0 0 1px #e6e6e6, 2px 2px 0 0 #cacaca; }
    @media screen and (min-width: 64em) {
      .partner-card:hover, .partner-card:focus {
        box-shadow: 0 0 0 1px #e6e6e6, 3px 3px 0 0 #cacaca; } }
  .partner-card--short {
    padding: 0;
    box-shadow: none; }
    @media screen and (min-width: 48em) {
      .partner-card--short {
        padding: 0; } }
    [data-whatinput='mouse'] .partner-card--short,
    [data-whatinput='keyboard'] .partner-card--short {
      opacity: 0.9; }
      [data-whatinput='mouse'] .partner-card--short:hover, [data-whatinput='mouse'] .partner-card--short:focus,
      [data-whatinput='keyboard'] .partner-card--short:hover,
      [data-whatinput='keyboard'] .partner-card--short:focus {
        box-shadow: none;
        opacity: 1; }
  .partner-card__logo {
    max-width: 75rem;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    height: 80px;
    margin-bottom: 16px; }
    @media screen and (min-width: 48em) {
      .partner-card__logo {
        height: 150px; } }
    .partner-card--short .partner-card__logo {
      margin-bottom: 0;
      height: auto; }
    .partner-card__logo__image {
      flex: 0 0 100%;
      padding-left: 0.375rem;
      padding-right: 0.375rem;
      max-width: 100%; }
      @media screen and (min-width: 48em) {
        .partner-card__logo__image {
          padding-left: 0.75rem;
          padding-right: 0.75rem; } }
      .partner-card__logo__image img {
        width: auto;
        height: auto;
        max-height: 80px; }
        @media screen and (min-width: 48em) {
